<template>
  <div class="card">
    <div class="card-header">
      UpdateForm
    </div>
    <div class="card-body">
      <form v-if="board" v-on:submit.prevent="handleUpdate">
        <div class="form-group row">
          <label for="btitle" class="col-sm-2 col-form-label">제목</label>
          <div class="col-sm-10">
            <input type="text" class="form-control" id="btitle" v-model="board.btitle"/>
          </div>
        </div>
        <div class="form-group row">
          <label for="bcontent" class="col-sm-2 col-form-label">내용</label>
          <div class="col-sm-10">
            <input type="text" class="form-control" id="bcontent" v-model="board.bcontent"/>
          </div>
        </div>
        <div class="form-group row">
          <label for="battach" class="col-sm-2 col-form-label">첨부그림</label>
          <div class="col-sm-10">
            <input type="file" class="form-control-file" id="battach" ref="battach"/>
          </div>
        </div>
        <div>
            <img v-bind:src="`${baseURL}/board/battach/${board.bno}?jwt=${$store.state.authToken}`" alt="" width="200"/>
        </div>
        <div class="form-group row mt-3">
          <div class="col-sm-12 d-flex justify-content-center">
            <input type="submit" class="btn btn-info btn-sm mr-2" value="수정"/>
            <input type="button" class="btn btn-info btn-sm" value="취소" v-on:click="handleCancel"/>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script setup>
import axios from "axios";
import apiBoard from "@/apis/board";
import { ref } from "vue";
import { useRoute, useRouter } from "vue-router";

const board = ref(null);
const battach = ref(null);
const baseURL = ref(axios.defaults.baseURL);
const route = useRoute();
const router = useRouter();

apiBoard.readBoard(route.query.bno, false)
  .then(response => {
    board.value = response.data;
  })
  .catch(error => {
    console.log(error);
  });

async function handleUpdate() {
  try {
    const multipartFormData = new FormData();
    multipartFormData.append("bno", board.value.bno);
    multipartFormData.append("btitle", board.value.btitle);
    multipartFormData.append("bcontent", board.value.bcontent);
    if(battach.value.files.length != 0) {
      multipartFormData.append("battach", battach.value.files[0]);
    }
    await apiBoard.updateBoard(multipartFormData);
    router.push(`/menu07/board/read?bno=${route.query.bno}&pageNo=${route.query.pageNo}&hit=false`);
  } catch(error) {
    console.log(error);
  }
}

function handleCancel() {
  router.push(`/menu07/board/read?bno=${route.query.bno}&pageNo=${route.query.pageNo}&hit=false`);
}
</script>

