import axios from "axios";

//--------------------------------------------------------
function getBoardList(pageNo=1) {
  return axios.get("/board/list", {params:{pageNo}});
}
//--------------------------------------------------------
function readBoard(bno, hit) {
  return axios.get(`/board/${bno}?hit=${hit}`);
}
//--------------------------------------------------------
