/*
// raw/JSON 인코딩
function createBoard(board) {
  //body 내용: {"btitle":"xxx", "bcontent":"yyy", "bwriter":"zzz"}
  return axios.post("/board", board);
}
*/

// multipart/form-data 인코딩
function createBoard(multipartFormData) {
  return axios.post("/board/create", multipartFormData); 
}
//--------------------------------------------------------
/*
// raw/JSON 인코딩
function updateBoard(board) {
  //body 내용: {"btitle":"xxx", "bcontent":"yyy", "bwriter":"zzz"}
  return axios.put("/board", board);
}
*/

// multipart/form-data 인코딩
function updateBoard(multipartFormData) {
  //put은 multipart/form-data 인코딩을 본문에 넣을 수 없음
  return axios.post("/board/update", multipartFormData); 
}
//--------------------------------------------------------
function deleteBoard(bno) {
  return axios.delete("/board/" + bno);
}
//--------------------------------------------------------
function downloadBoardAttach(bno) {
  return axios.get("/board/battach/" + bno, {responseType: "blob"});
}
//--------------------------------------------------------
export default {
  getBoardList,
  readBoard,
  createBoard,
  updateBoard,
  deleteBoard,
  downloadBoardAttach,
};

