<template>
  <div class="card">
    <div class="card-header">
      List
    </div>
    <div class="card-body">
      <div v-if="page != null">
        <div class="mb-3">
          <router-link to="/menu07/board/writeForm" class="btn btn-success btn-sm">생성</router-link>
        </div>
        <table class="table table-sm table-striped table-bordered">
          <thead>
            <tr>
              <th class="text-center" style="width:70px">번호</th>
              <th class="text-center">제목</th>
              <th class="text-center" style="width:90px">글쓴이</th>
              <th class="text-center" style="width:120px">날짜</th>
              <th class="text-center" style="width:70px">조회수</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="board in page.boards" v-bind:key="board.bno">
              <td class="text-center">{{board.bno}}</td>
              <td>
                <router-link v-bind:to="`/menu07/board/read?bno=${board.bno}&pageNo=${page.pager.pageNo}&hit=true`">
                  {{board.btitle}}
                </router-link>
              </td>
              <td class="text-center">{{board.mid}}</td>
              <td class="text-center">{{new Date(board.bdate).toLocaleDateString()}}</td>
              <td class="text-center">{{board.bhitcount}}</td>
            </tr>
            <tr>
              <td colspan="5" style="text-align: center;">
                <button v-on:click="changePageNo(1)"
                        class="btn btn-outline-primary btn-sm mr-1">처음</button> 
                <button v-if="page.pager.groupNo > 1"
                        v-on:click="changePageNo(page.pager.startPageNo-1)"
                        class="btn btn-outline-info btn-sm mr-1">이전</button>
                <button v-for="pageNo in range(page.pager.startPageNo, page.pager.endPageNo)" v-bind:key="pageNo" 
                        v-on:click="changePageNo(pageNo)"
                        v-bind:class="`btn ${(pageNo === page.pager.pageNo)?'btn-danger':'btn-outline-success'} btn-sm mr-1`">{{pageNo}}</button>
                <button v-if="page.pager.groupNo < page.pager.totalGroupNo" 
                        v-on:click="changePageNo(page.pager.endPageNo+1)"	
                        class="btn btn-outline-info btn-sm mr-1">다음</button>
                <button v-on:click="changePageNo(page.pager.totalPageNo)"
                        class="btn btn-outline-primary btn-sm">맨끝</button>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script setup>
import apiBoard from "@/apis/board";
import { ref, watch } from "vue";
import { useRoute, useRouter } from "vue-router";

const page = ref(null);
const route = useRoute();
const router = useRouter();

let pageNo = route.query.pageNo;
if(pageNo === 'undefined') {
  pageNo = 1;
}

getBoardList(pageNo);

function getBoardList(pageNo) {
  apiBoard.getBoardList(pageNo)
    .then(response => {
      page.value = response.data;
    })
    .catch(error => {
      console.log(error);
    });
}

function changePageNo(pageNo) {
  //같은 컴포넌트 내에서 URL만 변경
  router.push(`/menu07/board/list?pageNo=${pageNo}`);
}

//페이지 번호 배열 생성
function range(start, end) {
  const numbers = [];
  for (var i=start; i<=end ; i++) {
    numbers.push(i);
  }
  return numbers;
}

watch(route, (newUrl, oldUrl) => {
  if(newUrl.query.pageNo) {
    //URL이 변경되면 해당 페이지 내용을 가져오기
    getBoardList(newUrl.query.pageNo);
  } else {
    //pageNo가 없을 경우 1 페이지 내용을 가져오기
    getBoardList(1);
  }
});
</script>

