<template>
  <div class="card">
    <div class="card-header">
      Exam01AsyncControl
    </div>
    <div class="card-body">
      <div class="mb-3">
        <div>
          <button v-on:click="handleBtn1" class="btn btn-primary btn-sm mr-2">비동기 작업1</button>
          <button v-on:click="handleBtn2" class="btn btn-primary btn-sm mr-2">비동기 작업2</button>
          <div v-if="loading" class="mt-3">
            <div class="spinner-border text-info">
              <span class="sr-only">Loading...</span>
            </div> 
          </div>
          <div v-if="!loading" class="mt-3">result: {{result}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from "vue";

const loading = ref(false);
const result = ref("");

function work() {
  const promise = new Promise((resolve, reject) => {
    setTimeout(() => {
      resolve("value1");
    }, 3000);
  });
  return promise;  
}

function handleBtn1() {
  loading.value = true;
  work()
  .then((data) => {
    result.value = data;
  })
  .catch((reason) => {
    console.log(reason);
  })
  .finally(() => {
    loading.value = false;
  });
}

async function handleBtn2() {
  loading.value = true;
  try {
    var data = await work();
    result.value = data;
  } catch(err) {
    console.log(err);
  }
  loading.value = false;
}
</script>

